/* eslint-disable */
console.log("Script loaded");

import "@babel/polyfill";
import { login, logout } from "./login";
import { updateData, updatePassword } from "./updateSettings";
import "./modal"; // Import your modal functionality

// Values

const loginForm = document.querySelector(".form");
const signOut = document.querySelector(".signout-btn");
const userDataForm = document.querySelector(".user-info-form");
const userPasswordForm = document.querySelector(".change-password-form");

if (loginForm)
  loginForm.addEventListener("submit", (e) => {
    e.preventDefault();
    const email = document.getElementById("email").value;
    const password = document.getElementById("password").value;
    login(email, password);
  });

if (signOut) signOut.addEventListener("click", logout);

if (userDataForm) {
  userDataForm.addEventListener("submit", (e) => {
    e.preventDefault();
    console.log("Form submitted!");

    const email = document.getElementById("email").value;
    const name = document.getElementById("username").value;
    updateData(name, email);
  });
}

if (userPasswordForm) {
  userPasswordForm.addEventListener("submit", (e) => {
    e.preventDefault();
    console.log("Form submitted!");

    const passwordCurrent = document.getElementById("password-current").value;

    const password = document.getElementById("password").value;
    const passwordConfirm = document.getElementById("password-confirm").value;
    updatePassword(passwordCurrent, password, passwordConfirm);
  });
}
