const openCreatePostModal = document.querySelector(".create-post-cta");
const closeSubmitArticleModal = document.querySelector(
  "#closeSubmitArticleModal"
);
const submitArticleModal = document.querySelector("#submitArticleModal");

openCreatePostModal.addEventListener("click", () => {
  console.log("button working");
  submitArticleModal.style.display = "block";
});

closeSubmitArticleModal.addEventListener("click", () => {
  console.log("button working");
  submitArticleModal.style.display = "none";
});

window.addEventListener("click", (event) => {
  if (event.target === submitArticleModal) {
    submitArticleModal.style.display = "none";
  }
});
